<template>
  <div id="school">
    <common-header title="校园创业" link="我的申请" @linkClick="linkToUrl({name: 'SchoolDetail',query:{action:2}})"></common-header>

    <div class="school__search border-1px">
      <div class="search__input">
        <input class="search__input__value" type="text" v-model="searchKey" ref="search">
        <div class="search__input__icon"></div>
        <div class="search__input__del" v-show="searchKey.length > 0" @click="delSearchKey"></div>
      </div>
      <div class="search__btn"></div>
    </div>

    <div class="school__list">
      <div class="list" v-for="(item,index) in searchSchool" :key="item.activityOrgId" @click="linkToUrl({name: 'SchoolList',query:{id:item.activityOrgId}})">
      <!--<div class="list" v-for="(item,index) in searchSchool" :key="item.activityOrgId" @click="getId">-->
        <div class="list__name" v-html="item.name"></div>
        <div class="list__icon">
          <i class="list__icon__image"></i>
          <span class="list__icon__num">{{item.praiseStat}}</span>
        </div>
      </div>
    </div>

    <div class="search__submit" @click="linkToUrl({name: 'SchoolApply'})">
      我要申请
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      searchKey: '',
      schoolInfos:[],
    }
  },
  computed:{
	searchSchool(){
		return this.schoolInfos.filter((item)=>{
			 return item.name.indexOf(this.searchKey) != -1
		}).map((item)=>{
//			console.log(item)
			let res = {
				name: item.name.replace(this.searchKey,'<span data-id="'+item.activityOrgId+'" style="color:#de7fef">'+this.searchKey+'</span>'),
				activityOrgId: item.activityOrgId,
				praiseStat: item.praiseStat,
				type:item.type
			}
			return res
		})
	},
  	/*searchSchool(){
  		return this.schoolInfos.filter((item)=>{
  			 return item.name.indexOf(this.searchKey) != -1
  		})
  	},*/
  },
	created(){
		this.getSchoolList()
	},
  methods: {
    /**
     * 删除搜索词事件
     *
     */
    delSearchKey() {
      let dom = document.querySelector('.search__input__value');
      this.searchKey = '';
      dom.focus();
    },
    getSchoolList(){
    	this.$Api.getSchoolList().then((res)=>{
    		console.log(res)
    		if(res.q.s == 0){
    			this.schoolInfos = res.q.schoolInfos
    		}
    	})
    },
    getId(e){
    	
    	console.log(e.target.children[0]);
    }
  }
}
</script>


<style lang="scss">
  #school {
    position: relative;
    min-height: 100%;
    padding-bottom: 2rem;
    background: #f0f0f0;

    .school__search {
    	position: fixed;
    	top: 1.17rem;
    	width: 100%;
    	z-index: 999;
      display: flex;
      align-items: center;
      justify-content: space-around;
      height: 1.33rem;
      background: #fff;

      .search__input {
        position: relative;
        /*width: 8rem;*/
       	width: 90%;
        height: 0.8rem;
        background: #f0f0f0;
        border-radius: 0.4rem;

        .search__input__icon {
          position: absolute;
          left: .27rem;
          top: 50%;
          transform: translateY(-50%);
          width: .4rem;
          height: .4rem;
          background: url('../../assets/school/search.png') no-repeat center/100%;
        }

        .search__input__value {
          padding-left: .96rem;
          width: 100%;
          height: .8rem;
          line-height: .8rem;
          font-size: .4rem;
        }

        .search__input__del {
          position: absolute;
          right: .27rem;
          top: 50%;
          transform: translateY(-50%);
          width: .4rem;
          height: .4rem;
          background: url('../../assets/school/del.png') no-repeat center/100%;
        }
      }

      .search__btn {
        font-size: .4rem;
        color: #de7fef;
      }
    }

    .school__list {
			position: absolute;
			top: 1.33+1.17rem;
			width: 100%;
			bottom: 1.31rem;
			overflow-y: auto;
      .list {
        position: relative;
        display: flex;
        height: 1.21rem;
        padding: .4rem;
        justify-content: space-between;
        align-items: center;
        background: #fff;
        color: #333;

        .list__name {
          font-size: 0.4rem;
        }

        .list__icon {
          display: flex;
          justify-content: center;
          align-items: center;

          .list__icon__image {
            display: inline-block;
            margin-right: .2rem;
            width: 0.37rem;
            height: 0.37rem;
            background: url('../../assets/school/like_un.png') no-repeat center/100%;

            &.active {
              background: url('../../assets/school/like.png') no-repeat center/100%;
            }
          }

          .list__icon__num {
            font-size: 0.37rem;
          }
        }

        &::after {
          content: '';
          position: absolute;
          bottom: 0;
          left: -50%;
          width: 200%;
          border-bottom: 1px solid #ccc;
          transform: scale(0.5);
          transform-origin: center;
        }
      }
    }

    .search__submit {
      position: fixed;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 1.31rem;
      line-height: 1.31rem;
      text-align: center;
      font-size: .45rem;
      color: #fff;
	    background-image: linear-gradient(135deg, #fe9bba 0%, #de7fef 100%);
    }
  }
</style>




